import React, { useEffect, useState } from 'react'

import MainNavbar from '../../../../layouts/pages/Navbar/MainNavbar'
import Layoutheading from '../../../../layouts/components/Layoutheading/Layoutheading'
import { Container,Row,Col, Card, Spinner } from 'react-bootstrap'
import ProductSideBar from '../../../components/ProductSideBar'
import { CategoryResponseView, SubCategoriesView } from '../../../../categories/models/CategoryResponseView'
import Noproducts from '../../../../ui/components/No_Products/Noproducts'
import { Appdispatch, RootState, useAppDispatch } from '../../../../../Redux/store'
import * as categoryactions from "../../../../../Redux/categories/category.actions"
import * as categoryReducer from "../../../../../Redux/categories/category.reducer"
import { useSelector } from 'react-redux'
import * as productactions from "../../../../../Redux/products/product.actions"
import * as productReducer from "../../../../../Redux/products/product.reducer"
import { ProductResponseView } from '../../../models/ProductResponseView'
import { Link } from 'react-router-dom'
import { Button } from 'react-bootstrap'
import SpinnerUI from '../../../../ui/components/SpinnerUI/SpinnerUI'
import { addToCartAction } from '../../../../../Redux/carts/cart.reducer'
import { CartReduxService } from '../../../../../Redux/carts/cartReduxService'

const Householdcatelogue = () => {
  const dispatch:Appdispatch=useAppDispatch();

const productReduxState:productReducer.IntialState = useSelector((state:RootState)=>{
  return state[productReducer.productFeatureKey]
})
  //get the data from redux
  const categoryReduxState:categoryReducer.IntialState = useSelector((state:RootState)=>{
    return state[categoryReducer.categoryFeatureKey]
  })

  const {products:reduxProducts} = productReduxState;
  const{loading,categories} = categoryReduxState;


  const [category,setCategory] = useState<CategoryResponseView>({} as CategoryResponseView)
  const [subCategories,setSubCategories] = useState<SubCategoriesView[]>([])
  const [products,setProducts] = useState<ProductResponseView[]>([])
  const [filterLoading,setFilterLoading] = useState<boolean>(false);
    const [filterProducts,setFilterProducts] = useState<ProductResponseView[]>([] as ProductResponseView[])
  

 useEffect(()=>{
   dispatch(categoryactions.getAllCategoriesAction())
 },[])

 //from categories get single category and sub categories

 useEffect(()=>{
    if(categories.length>0){
      const category:CategoryResponseView|undefined = categories.find(item => item.name.toLowerCase().trim().includes("household"));
      if(category){
        setCategory(category);
        if(category.subCategories){
          setSubCategories(category.subCategories.map(item=>{
            return{
              _id:item._id,
              name: item.name,
              description:item.description,
              isChecked:true
            }
          }))
        }
      }
    
    }
 },[categories])

  //when single category changes get all products with categoryId
 useEffect(()=>{
  if(category && Object.keys(category).length>0){
    dispatch(productactions.getAllProductsWithCategoryIdAction({categoryId : category._id}))
  }
 },[category])

//when the redux product changes
useEffect(()=>{
     if(reduxProducts.length>0){
      setProducts(reduxProducts);
      setFilterProducts(reduxProducts);
     }
},[reduxProducts])
 //filtered the products on selected subcategories
  const filteredTheProducts = (subCategories:SubCategoriesView[])=>{
    let Subs = subCategories.map(item => {
      if(item.isChecked){
        return item._id
        }
     }).filter(item=> item !== undefined)
   
   console.log("Products array:", products);
   console.log("SubCategory IDs for filtering:", Subs);
   
    setFilterLoading(true);
    setTimeout(()=>{
      
      setFilterProducts(products.filter(item => Subs.includes(item?.subCategoryObj?._id)))
      setFilterLoading(false)
      
    },300)
   
  
  }
  const ClickAddToCart=(product:ProductResponseView)=>{
    dispatch({
      type:`${addToCartAction}`,
      payload:{product:CartReduxService.convertToProductEntity(product),count:1}
    });
  }
    
  return (
    
    
     <div>
      {loading && <SpinnerUI/>}
     <MainNavbar/>
    
                    <Container fluid>
                      <Row>
                            <Col xs={1}>
                                <ProductSideBar subCategories={subCategories} setSubCategories ={setSubCategories}
                                filteredTheProducts={filteredTheProducts}/>
                            </Col>
                            <Col>
                                <Layoutheading heading={"Household catalogue"}/>
                            </Col>
                      </Row>
                      </Container>
                      {
                                    filterProducts && filterProducts.length>0 &&
                                    <Container>
                                      <Row>
                                        {
                                          filterProducts.map(product =>{
                                            return(
                                              <Col xs={3} key={product._id} className='mb-3 text-center'>
                                                <Card>
                                                  <Card.Header>
                                                        <Link to={`/products/view/${category.name}/${product._id}`}>
                                                        <img src={product.imageurl} alt='/' 
                                                        width={180} height={200} className='text-center m-auto d-block'></img>
                                                        </Link>
                                                  </Card.Header>
                                                  <Card.Body>
                                                      <small className='fw-bold text-success'>{product.title}</small><br/>
                                                      <small className='fw-bold text-danger'>&#8377;{Number(product.price).toFixed(2)}</small><br/>
                                                      <Button variant={'warning'} size={'sm'} onClick={()=>ClickAddToCart(product)} >Add to Cart</Button>
                                                  </Card.Body>
                                                </Card>
                                              </Col>
                                            )
                                          })
                                        }
                                      </Row>
                                      </Container>
                                    }
                                    {
                                      products.length === 0 &&
                                      <Noproducts/>
                                    }

      
      
       
          
     </div>
    
  )
}

export default Householdcatelogue
