import axios from "axios";
import { CategoryResponseView } from "../models/CategoryResponseView";
import { CategoryRequestView } from "../models/CategoryRequestView";

export class CategoryService{
    private static serverUrl :string =  process.env.REACT_APP_EXPRESS_SERVER_URL? process.env.REACT_APP_EXPRESS_SERVER_URL: "";
    /**
 * @usage: create category
 * @url: http://localhost:9000/categories/
 * @params: name,description
 * @method: post
 * @access: private
 */
    public static createCategory(category:CategoryRequestView):Promise<{data:{status:string,data:CategoryResponseView,msg:string}}>{
        const dataUrl=`${this.serverUrl}/api/categories/`;
        return axios.post(dataUrl,category);
    } ;
    /**
 * @usage: create sub category
 * @url: http://localhost:9000/api/categories/:categoryId
 * @params: name,description
 * @method: post
 * @access: private
 */
    public static createSubCategory(category:CategoryRequestView,categoryId:string):Promise<{data:{status:string,msg:string,data:CategoryResponseView}}>{
        const dataUrl=`${this.serverUrl}/api/categories/${categoryId}`;
        return axios.post(dataUrl,category);
    } ;
/**
 * @usage: get all categories
 * @url: http://localhost:9000/api/categories/
 * @params: no-params
 * @method: get
 * @access: public
 */
public static getAllCategories():Promise<{data:{status:string,msg:string,data:CategoryResponseView}}>{
    const dataUrl=`${this.serverUrl}/api/categories/`;
    return axios.get(dataUrl);
} ;

}