import React, { useEffect, useRef } from 'react'
import MainNavbar from '../../../layouts/pages/Navbar/MainNavbar'
import Layoutheading from '../../../layouts/components/Layoutheading/Layoutheading'
import { Container, ListGroup } from 'react-bootstrap'
import { Card, Col, Row } from 'react-bootstrap'
import { Button } from 'react-bootstrap'
import { Link, useNavigate, useParams } from 'react-router-dom'
import {  useSelector } from 'react-redux'
import { Appdispatch, RootState, useAppDispatch } from '../../../../Redux/store'
import * as userReducer from "../../../../Redux/users/user.reducer";
import * as useractions from "../../../../Redux/users/user.actions";
import SpinnerUI from '../../../ui/components/SpinnerUI/SpinnerUI'
declare const window: Window &
    typeof globalThis & {
        cloudinary:{
            createUploadWidget:(p:{},p1:(error:any,result:any)=>void) => any
        }
    }
const UserProfile = () => {
 
  const navigate=useNavigate();

  const dispatch:Appdispatch= useAppDispatch();
  const cloudinaryRef = useRef<any>()
  const widgetRef = useRef<any>()

  //get the userinfo from Redux
  const userReduxState :userReducer.IntialState=useSelector((State:RootState)=>{
    return State[userReducer.userFeatureKey]
  })
  const{loading,user,address}=userReduxState;

  useEffect(()=>{
    dispatch(useractions.getAddressAction())
  },[])
  const deleteHandler=(addressId:string|undefined)=>{
   if(addressId){
    dispatch(useractions.DeleteAddressAction({addressId:addressId})).then((response:any)=>{
     if(response && !response.error){
      dispatch(useractions.getAddressAction())
    
     }
    })
   }
  }

  useEffect(()=>{
    cloudinaryRef.current = window.cloudinary;
    widgetRef.current = cloudinaryRef?.current?.createUploadWidget({
    cloudName: process.env.REACT_APP_CLOUDINARY_CLOUD_NAME,
    uploadPreset: process.env.REACT_APP_CLOUDINARY_UPLOAD_PRESET 
    }, (error: any, result: { event: string; info: { secure_url: any; }; }) => { 
        if (!error && result && result.event === "success") {
          const imageUrl=result.info.secure_url
          console.log("userprofile",imageUrl);
          dispatch(useractions.updateProfilePicAction({
          
            imageurl:imageUrl}))
            console.log("after userprofile",imageUrl);
            
        }
})
  },[])
  
  const clickEditImage=()=>{
    widgetRef.current.open();

  }
  return (
    <div>
      {loading && <SpinnerUI/>}
      <MainNavbar/>
      <Layoutheading heading={"UserProfile"}  icon={"bi-person"}/>
      <Container>
        <Row>
          {
               user && Object.keys(user).length>0 &&
            <>

          <Col xs={2}>
          <Card>
            <Card.Header className='bg-warning'>
              <p className='h4 '>Profile</p>
              </Card.Header>
              <Card.Body>
                <img src={user.imageurl} className='img-fluid' alt="/"></img>
              <Button type="button" variant={'warning'} className='mt-2' onClick={clickEditImage}>
                <i className='bi bi-pencil-square'>Edit</i>
              </Button>
              </Card.Body>
          </Card>
          </Col>
          <Col xs={4}>
          <Card>
          <Card.Header className='bg-primary'>
            <div >
            <p className='h4 text-white ' >Your Information</p>
            </div>
          </Card.Header>
          <Card.Body>
                    <ListGroup>
                <ListGroup.Item>Name:
                  <span className='fw-bold'>{user.username}</span>
                </ListGroup.Item>
                <ListGroup.Item>Email:
                  <span className='fw-bold'>{user.email}</span>
                </ListGroup.Item>
                <ListGroup.Item>Admin:
                  <span className='fw-bold'>{user.isAdmin?"Yes": "No"}</span>
                </ListGroup.Item>
                <ListGroup.Item>SuperAdmin:
                  <span className='fw-bold'>{user.isSuperAdmin?"Yes":"No"}</span>
                </ListGroup.Item>
              </ListGroup>
            
          </Card.Body>
          </Card>
          </Col>
          </>
          }
          <Col xs={5}>
          <Card>
            <Card.Header className='bg-success'>
              <div className='d-flex justify-content-between'>
                <p className='h4 text-white'>Shipping Address</p>
             <div>
            {
              address && Object.keys(address).length>0 ?
              <>
               <Link to={`/users/edit-shipping-address/${address._id}`} className='me-2'>
               <Button variant='warning'>
                     <i className='bi bi-pencil-fill text-white'></i>
               </Button>
               </Link>
              
               <Button variant='danger' onClick={e=>deleteHandler(address._id)} >
                     <i className='bi bi-trash-fill'></i>
               </Button>
               
               </>:
               <Link to={"/users/add-shipping-address"} className='me-2'>
               <Button variant='primary'>
                     <i className='bi bi-plus-circle-fill'></i>
               </Button>
               </Link>
               
            }
             </div>
             </div>
              </Card.Header>
            {
              address && Object.keys(address).length>0 &&
                <Card.Body>
                       <ListGroup>
                <ListGroup.Item>Mobile:
                  <span className='fw-bold'>{address.mobile}</span>
                </ListGroup.Item>
                <ListGroup.Item>Flat:
                  <span className='fw-bold'>{address.flat}</span>
                </ListGroup.Item>
                <ListGroup.Item>Street:
                  <span className='fw-bold'>{address.street}</span>
                </ListGroup.Item>
                <ListGroup.Item>LandMark:
                  <span className='fw-bold'>{address.landmark}</span>
                </ListGroup.Item>
                <ListGroup.Item>City:
                  <span className='fw-bold'>{address.city}</span>
                </ListGroup.Item>
                <ListGroup.Item>State:
                  <span className='fw-bold'>{address.state}</span>
                </ListGroup.Item>
                <ListGroup.Item>Country:
                  <span className='fw-bold'>{address.country}</span>
                </ListGroup.Item>
                <ListGroup.Item>Pincode:
                  <span className='fw-bold'>{address.pincode}</span>
                </ListGroup.Item>
               
               </ListGroup>
                </Card.Body>
            }
          </Card>
          </Col>
        </Row>
      </Container>
    </div>
  )
}

export default UserProfile
