import React, { useEffect } from 'react'
import MainNavbar from '../../../layouts/pages/Navbar/MainNavbar'
import Layoutheading from '../../../layouts/components/Layoutheading/Layoutheading'
import { CardBody, Container, ListGroup, ListGroupItem } from 'react-bootstrap'
import { Card, CardHeader, Col, Row } from 'react-bootstrap'
import { Button } from 'react-bootstrap'
import { Link, Navigate, useNavigate } from 'react-router-dom'
import * as useractions from "../../../../Redux/users/user.actions";
import * as userReducer from "../../../../Redux/users/user.reducer" 
import { Appdispatch, RootState, useAppDispatch } from '../../../../Redux/store'
import { useSelector } from 'react-redux'
import * as cartactions from "../../../../Redux/carts/cart.actions"
import * as cartReducer from "../../../../Redux/carts/cart.reducer";
import SpinnerUI from '../../../ui/components/SpinnerUI/SpinnerUI'
import { CartReduxService } from '../../../../Redux/carts/cartReduxService'
import { OrderRequestView } from '../../../orders/models/OrderRequestView'
import { TheProduct } from '../../models/CartResponseView'
import { orderProduct } from '../../../orders/models/OrderRequestView'
import * as orderactions from "../../../../Redux/orders/orders.actions";
import { error } from 'console'

const CheckOutPage = () => {
  const dispatch:Appdispatch =useAppDispatch();
  const navigate=useNavigate();

  const userReduxState:userReducer.IntialState =useSelector((state:RootState)=>{
    return state[userReducer.userFeatureKey]
  })
  const cartReduxState:cartReducer.IntialState = useSelector((State:RootState)=>{
    return State[cartReducer.cartFeatureKey]
  })

  const{address,user,loading}=userReduxState;
  const{loading:cartloading,cartproduct}=cartReduxState;

useEffect(()=>{
  dispatch(useractions.getAddressAction())
  dispatch(cartactions.getCartInfoAction())
},[])
const getOrderProducts=(products:TheProduct[]):orderProduct[]=>{
     const orderProducts:orderProduct[]=products.map(product=>{
      return{
        product:product._id,
        count:product.count,
        price:product.price
      }
    
     })
     return orderProducts;
     
     
}
const clickPlaceOrder=()=>{
 if(user._id){
  const order:OrderRequestView ={
    products:getOrderProducts(cartproduct.products),
    total:CartReduxService.calculateTotal(cartproduct.products),
    tax:CartReduxService.calculateTax(cartproduct.products),
    grandtotal:CartReduxService.calculategrandTotal(cartproduct.products),
    orderBy: user._id,
    paymentType:"COD"
  }
  dispatch(orderactions.placeOrderAction({order:order})).then((response:any)=>{
    if(response && !response.error){
         navigate("/orders/details")
         dispatch({
          type:`${cartReducer.clearCartAction}`
         })
    }
  })
}
 }

  return (
    <div>
      { (loading || cartloading) && <SpinnerUI/>}
    <MainNavbar/>
      <Layoutheading heading={"Checkout Here"} icon={"bi-list"}/>
      <Container>
        <Row>
           <Col sm={7}>
            {
              address && Object.keys(address).length > 0 &&
              <Card>
            <Card.Header className='bg-success text-white  d-flex justify-content-between' >
              <h3>Shipping Address</h3>
             <Link to={`user/edit-shipping-address/${address._id}`}>
             <Button variant={'primary'}>
              <i className='bi bi-pencil'></i>
             </Button>
             </Link>
            </Card.Header>
            <Card.Body>
                   <ListGroup>
                    <ListGroupItem>
                      Name :
                      <span className='fw-bold'>{address.name}</span>
                    </ListGroupItem>
                    <ListGroupItem>
                      Mobile No :
                      <span className='fw-bold'>{address.mobile}</span>
                    </ListGroupItem>
                    <ListGroupItem>
                      Flat No :
                      <span className='fw-bold'>{address.flat}</span>
                    </ListGroupItem>
                    
                    <ListGroupItem>
                      Landmark :
                      <span className='fw-bold'>{address.landmark}</span>
                    </ListGroupItem>
                    <ListGroupItem>
                      Street :
                      <span className='fw-bold'>{address.street}</span>
                    </ListGroupItem>
                    <ListGroupItem>
                      City :
                      <span className='fw-bold'>{address.city}</span>
                    </ListGroupItem>
                    <ListGroupItem>
                      State :
                      <span className='fw-bold'>{address.state}</span>
                    </ListGroupItem>
                  
                   <ListGroupItem>
                      Country :
                      <span className='fw-bold'>{address.country}</span>
                    </ListGroupItem>
                    <ListGroupItem>
                      Pincode :
                      <span className='fw-bold'>{address.pincode}</span>
                    </ListGroupItem>
                   
                    </ListGroup>
                    
            </Card.Body>
           </Card>
            }
           </Col>
           <Col sm={5}>
           {
            cartproduct.products && Object.keys(cartproduct.products).length>0 &&
            <Card>
            <Card.Header className='bg-success text-white  ' >
              <h3>Your Cart</h3>
            
            </Card.Header>
            <Card.Body>
                   <ListGroup>
                  {
                    cartproduct.products && cartproduct.products.map((product)=>{
                      return(
                        <ListGroupItem key={product._id}>
                        <Row>
                          <Col>
                          <img src={product.imageurl} alt=""  width={50} height={50}></img>
    
                          </Col>
                          <Col>
                          <small>{product.title}|{product.brand}</small><br/>
                          <small>Count:{product.count}</small><br/>
                          <small>Price:{product.price}</small><br/>
                          </Col>
                        </Row>
                        </ListGroupItem>
                      )
                    })
                  }
                   </ListGroup>
                    <ListGroup>
                                 <ListGroupItem >Total:<span className='fw-bold'>&#8377;{CartReduxService.calculateTotal(cartproduct.products).toFixed(2)}</span></ListGroupItem>
                                 <ListGroupItem>Tax:<span className='fw-bold'>&#8377;{CartReduxService.calculateTax(cartproduct.products).toFixed(2)}</span></ListGroupItem>
                                 <ListGroupItem>GrandTotal:<span className='fw-bold'>&#8377;{CartReduxService.calculategrandTotal(cartproduct.products).toFixed(2)}</span></ListGroupItem>
                                 
                                </ListGroup>
                  <div className='d-grid'>
                  <Button variant='warning' className='mt-3' onClick={clickPlaceOrder}>Place Order </Button>
                  </div>
            </Card.Body>
           </Card>
           }
           </Col>
        </Row>
      </Container>pro
    
    </div>
  )
}


export default CheckOutPage
