import axios from "axios";
import { ProductRequestView } from "../models/ProductRequestView";
import { ProductResponseView } from "../models/ProductResponseView";
import { ProductView } from "../models/ProductView";

export class ProductService{
    private static serverUrl :string =  process.env.REACT_APP_EXPRESS_SERVER_URL? process.env.REACT_APP_EXPRESS_SERVER_URL: "";
    /**
 * @usage: create products
 * @url: http://localhost:9000/api/products/
 * @params: title,description,imageUrl,brand,price,quantity,categoryId,subcategoryId
 * @method: post
 * @access: private
 */
    public static createProduct(product:ProductRequestView):Promise<{data:{status:string,data:ProductView,msg:string}}>{
        const dataUrl=`${this.serverUrl}/api/products`;
        return axios.post(dataUrl,product);
    } ;
    /**
 * @usage: update products
 * @url: http://localhost:9000/api/products/:productId
 * @params: title,description,imageUrl,brand,price,quantity,categoryId,sucCategoryId
 * @method: put
 * @access: private
 */
    public static updateProduct(products:ProductRequestView,productId:string):Promise<{data:{status:string,data:ProductView,msg:string}}>{
        const dataUrl=`${this.serverUrl}/api/products/${productId}`;
        return axios.put(dataUrl,products);
    } ;
    /**
 * @usage:get product
 * @url: http://localhost:9000/api/products/:productId
 * @params: no-params
 * @method: get
 * @access: private
 */
    public static getProduct(productId:string):Promise<{data:{status:string,data:ProductResponseView,msg:string}}>{
        const dataUrl=`${this.serverUrl}/api/products/${productId}`;
        return axios.get(dataUrl);
    } ;
    /**
 * @usage:get all products
 * @url: http://localhost:9000/api/products/
 * @params: no-params
 * @method: get
 * @access: private
 */
    public static getAllProducts():Promise<{data:{status:string,data:ProductResponseView[],msg:string}}>{
        const dataUrl=`${this.serverUrl}/api/products/`;
        return axios.get(dataUrl);
    } ;
    /**
 * @usage:delete a product
 * @url: http://localhost:9000/products/:productId
 * @params: no-params
 * @method: delete
 * @access: private
 */
    public static deleteProduct(productId:string):Promise<{data:{status:string,data:ProductView,msg:string}}>{
        const dataUrl=`${this.serverUrl}/api/products/${productId}`;
        return axios.delete(dataUrl);
    } ;
    /**
 * @usage:get all products with categoryId
 * @url: http://localhost:9000/api/products/categories/categoryId
 * @params: no-params
 * @method: get
 * @access: private
 */
    public static getAllProductsWithCategoryId(categoryId:string):Promise<{data:{status:string,data:ProductResponseView[],msg:string}}>{
        const dataUrl=`${this.serverUrl}/api/products/categories/${categoryId}`;
        return axios.get(dataUrl);
    } ;
}