import { APP_CONSTANTS } from "../constants";

export class TokenUtil{
    
    public static saveTokenToSession(token:string){
        sessionStorage.setItem(APP_CONSTANTS.REACT_APP_JWT_TOKEN,token);
        console.log("token",token)
    }
    public static removeTokenFromSession(){
        sessionStorage.removeItem(APP_CONSTANTS.REACT_APP_JWT_TOKEN);
    }
    public static getToken(){
       return sessionStorage.getItem(APP_CONSTANTS.REACT_APP_JWT_TOKEN);
    }
    public static IsLoggedIn(){
        const token = sessionStorage.getItem(APP_CONSTANTS.REACT_APP_JWT_TOKEN);
      
           return !!token;
        
    }

}