import React, { useState } from 'react'
import MainNavbar from '../../../layouts/pages/Navbar/MainNavbar'
import Layoutheading from '../../../layouts/components/Layoutheading/Layoutheading'
import { Container } from 'react-bootstrap';
import SpinnerUI from '../../../ui/components/SpinnerUI/SpinnerUI';
import { Col, Row } from 'react-bootstrap';
import { Form } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { Appdispatch, RootState, useAppDispatch } from '../../../../Redux/store';
import * as userReducer from "../../../../Redux/users/user.reducer"
import * as useractions from "../../../../Redux/users/user.actions"
import { useSelector } from 'react-redux';
import { Userview } from '../../models/Userview';
import { Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { toastUtil } from '../../../../Util/ToastUtil';

 interface IState{
  password:string,
  conformpassword:string
 }

const Userpassword = () => {
  const navigate =useNavigate();
  const dispatch:Appdispatch= useAppDispatch();
 

  const [validated, setValidated] = useState(false);

  //get the data from store
  const userState:userReducer.IntialState = useSelector((state:RootState)=>{
    return state[userReducer.userFeatureKey]
  })
  const {loading} =userState

  const [user,setUser]= useState<IState>({
    
    
    password:"",
    conformpassword:""
  })
  
  const updateInput=(event:React.ChangeEvent<HTMLInputElement>)=>{
    setUser({
      ...user,
      [event.target.name]:event.target.value
    })
  }
  const handleSubmit = (event:React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const form = event.currentTarget;
    
    if(form.checkValidity() === true){
            if(user.password === user.conformpassword){
              dispatch(useractions.changepasswordAction({password:user.password})).then((response:any)=>{
            
                if(response && !response.error){
                  
                    dispatch({
                      type:`${userReducer.userLogoutAction}`
                    })
                    navigate('/')
                };
                
              
              })
            }
            else{
              toastUtil.displayErrorMessage("Both passwords are not matched");
            }
   
    }
    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    }

    setValidated(true);
  };
  return (
    <div>
      <MainNavbar/>
      <Layoutheading heading={"ChangePassword"} icon={"bi-eye-fill"}/>
      {loading && <SpinnerUI/>}
    <pre>{JSON.stringify(user)}</pre>
      <Container>
        <Row>
          <Col xs={4}>
                <Form noValidate validated={validated} onSubmit={handleSubmit}>
                                 <Form.Group className="mb-2">
                                         <Form.Control
                                         required
                                         pattern={"(?!.*\\s)(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])(?=.*[~!@#$%^&*()\\-=+{}\\[\\]|\\\\:;\"'<>,.?/_]).{6,15}"}
                                          type="password" 
                                          value={user.password}
                                          name={'password'} placeholder="New password"onChange={updateInput}/>
                                          <Form.Control.Feedback type='invalid'>choose valid email</Form.Control.Feedback>
                                 </Form.Group>
                                
                                 <Form.Group className="mb-2">
                                         <Form.Control
                                         required
                                         pattern={"(?!.*\\s)(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])(?=.*[~!@#$%^&*()\\-=+{}\\[\\]|\\\\:;\"'<>,.?/_]).{6,15}"}
                                          type="password"
                                          value={user.conformpassword}
                                          name={'conformpassword'} placeholder="Conform password"onChange={updateInput}/>
                                          <Form.Control.Feedback type='invalid'>choose valid password </Form.Control.Feedback>
                                 </Form.Group>
                                 <Button variant="success" type="submit">
                                   Change password
                                 </Button>
                                 <Link to={'/'} className="btn btn-dark ms-2">Cancel</Link>
                                 
                               </Form>
              

          </Col>
        </Row>
      </Container>
    
    </div>
  )
}


  

export default Userpassword
