import React, { ChangeEvent, useEffect, useRef, useState } from 'react'
import MainNavbar from '../../../layouts/pages/Navbar/MainNavbar'
import Layoutheading from '../../../layouts/components/Layoutheading/Layoutheading'
import * as categoryReducer from "../../../../Redux/categories/category.reducer"
import * as categoryactions from "../../../../Redux/categories/category.actions"
import { Appdispatch, RootState, useAppDispatch } from '../../../../Redux/store'
import { useSelector } from 'react-redux'
import { Card, Container, ListGroup, ListGroupItem } from 'react-bootstrap'
import { Col, Row } from 'react-bootstrap'
import { Form } from 'react-bootstrap'
import { CategoryResponseView, SubCategoriesView } from '../../models/CategoryResponseView'
import { Button } from 'react-bootstrap'

const Addcategory = () => {
      const dispatch:Appdispatch = useAppDispatch();
     
 //get the data from redux store
   const categoryReduxState:categoryReducer.IntialState = useSelector((state:RootState)=>{
        return state[categoryReducer.categoryFeatureKey]
   })
   const {loading,categories} = categoryReduxState;


   const [validated,setValidated]= useState(false)
   const [categoryId,setCategoryId] =useState<string>()
   const [subCategories,setSubCategories]= useState<SubCategoriesView[]>()
   const[subCategory,setSubCategory]= useState<SubCategoriesView>({
      name:"",
      description:""
   })

  //get the data from server
  useEffect(()=>{
     dispatch(categoryactions.getAllCategoriesAction())
   },[])

   //change Handler on selectcategory

   const SelectCategory=(event:React.ChangeEvent<HTMLSelectElement|any>)=>{
        setCategoryId(event.target.value)
        setSubCategory({
          name:"",
          description:""
        })
      if(categories.length>0){
        let category:CategoryResponseView | undefined = categories.find(item =>item._id === event.target.value)
        if(category && category.subCategories){
          setSubCategories(category.subCategories)
        }else{
          setSubCategories([] as SubCategoriesView[])
        }
      }
   }
   //change handler on subcategory

   const subCategoryHandler=(event:React.ChangeEvent<HTMLInputElement|any>)=>{
       setSubCategory({
        ...subCategory,
          [event.target.name] : event.target.value
       })
   }
//Form submition for subcategories
const handleSubmit = (event:React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const form = event.currentTarget;
    
    if(form.checkValidity() === true){
    dispatch(categoryactions.createSubCategoryAction({category:subCategory,categoryId:categoryId})).then((response:any)=>{
      
      if(response && !response.error){
        
        dispatch(categoryactions.getAllCategoriesAction())
      };
      
    
    })
    }
    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    }

    setValidated(true);
  };
   
  return (
    <div>
      <MainNavbar/>
      <Layoutheading heading={"Add SubCategories"}/>
    
          <Container>
            <Row>
               <Col xs={4}>
               <Form noValidate validated={validated} onSubmit={handleSubmit}>
                      <Form.Group>
                        <Form.Select 
                        required
                        value={categoryId} onChange={event=>SelectCategory(event)}>
                          <option>Select Option</option>
                          
                          {
                            categories.map(category=>{
                              return(
                                <option key ={category._id} value={category._id}>{category.name}</option>
                              )
                            })
                          }
                        </Form.Select>
                        </Form.Group>
                              
                              <Form.Group className='mt-2'>
                                <Form.Control type='text'
                                required
                                value={subCategory.name} 
                                name='name'
                                 onChange={event=>subCategoryHandler(event)}
                                 placeholder='subcategory'></Form.Control>
                                </Form.Group>
                        
                                <Form.Group className='mt-2'>
                                <Form.Control
                                required
                                value={subCategory.description}
                                name='description' 
                                  onChange={e=>subCategoryHandler(e)}
                                  as ='textarea' placeholder='Description'></Form.Control>
                              </Form.Group>
                              <Form.Group className='mt-2'>
                                <Button variant={'success'} type='submit'>Create</Button>
                              </Form.Group>
                </Form>
               </Col>

               <Col xs={4}>
                  <Card>
                    <Card.Header>
                      <h4>All sub-categories</h4>
                    </Card.Header>
                 
                        <Card.Body>
                        <ListGroup>
                        {
                          subCategories && subCategories.map(subCategory=>{
                         return(
                          
                          <ListGroupItem key={subCategory._id}>{subCategory.name}</ListGroupItem>
                        
                         )
                          })
                        }
                        </ListGroup>
                        </Card.Body>
                        </Card>
               </Col>
            </Row>
          </Container>
    </div>
  )
}

export default Addcategory

