import React, { useEffect, useState } from 'react'
import MainNavbar from '../../../layouts/pages/Navbar/MainNavbar'
import Layoutheading from '../../../layouts/components/Layoutheading/Layoutheading'
import { Container } from 'react-bootstrap'
import { Col, Row } from 'react-bootstrap'
import { Form } from 'react-bootstrap'
import { Button } from 'react-bootstrap'
import { Link, useNavigate, useParams } from 'react-router-dom'
import { AddressView } from '../../models/AddressView'
import { Appdispatch, RootState, useAppDispatch } from '../../../../Redux/store'
import * as useractions from "../.././../../Redux/users/user.actions"
import * as userReducer from "../.././../../Redux/users/user.reducer"
import { useSelector } from 'react-redux'



const EditShippingAddress = () => {

  const{addressId} =useParams();
    const dispatch:Appdispatch =useAppDispatch();
    const navigate = useNavigate();

    //get the data from store
  const userState:userReducer.IntialState = useSelector((state:RootState)=>{
    return state[userReducer.userFeatureKey]
  })
  const {loading,address:reduxaddress} =userState
  
    const [validated, setValidated] = useState(false);
  
    const [address,setAddress] =useState<AddressView>({
      mobile:"",
      flat:"",
      street:"",
      landmark:"",
      city:"",
      state:"",
      country:"",
      pincode:""
  
  
    } as AddressView);
  
    const updateInput = (event:React.ChangeEvent<HTMLInputElement|any>)=>{
      setAddress({
        ...address,
        [event.target.name] :event.target.value
      })
    }
  const handleSubmit = (event:React.FormEvent<HTMLFormElement>) => {
      event.preventDefault();
      const form = event.currentTarget;
      
      if(form.checkValidity() === true){
      dispatch(useractions.updateAddressAction({address:address,addressId:addressId})).then((response:any)=>{
        
        if(response && !response.error){
          
            navigate('/users/profile')
        };
        
      
      })
      }
      if (form.checkValidity() === false) {
        event.preventDefault();
        event.stopPropagation();
      }
  
      setValidated(true);
    };
    useEffect(()=>{
      if(addressId){
        dispatch(useractions.getAddressAction())
      }
    },[addressId])

    useEffect(()=>{
          if(reduxaddress && Object.keys(reduxaddress).length>0){
            setAddress({

              ...address,
              mobile:reduxaddress.mobile,
              flat:reduxaddress.flat,
              street:reduxaddress.street,
              landmark:reduxaddress.landmark,
              city:reduxaddress.city,
              state:reduxaddress.state,
              country:reduxaddress.country,
              pincode:reduxaddress.pincode

            })
          }
    },[reduxaddress])
  return (
    <div>
        <MainNavbar/>
      <Layoutheading heading='Edit Shipping Address'/>
      <pre>{JSON.stringify(address)}</pre>
      <Container>
        <Row>
          <Col xs={5}>
          <Form noValidate validated={validated} onSubmit={handleSubmit}>
            <Form.Group className='mb-2'>
              <Form.Control 
              required
              name='mobile'
              value={address.mobile}
              onChange={e=>updateInput(e)}
              type={'number'} placeholder={'mobile'}></Form.Control>
            </Form.Group>
            <Form.Group className='mb-2'>
              <Form.Control
              required
              name='flat'
              value={address.flat}
              onChange={e=>updateInput(e)}
               type={'text'} placeholder={'flat'}></Form.Control>
            </Form.Group>
            <Form.Group className='mb-2'>
              <Form.Control 
              required
              name='landmark'
              value={address.landmark}
              onChange={e=>updateInput(e)}
              type={'text'} placeholder={'landmark'}></Form.Control>
            </Form.Group>
            <Form.Group className='mb-2'>
              <Form.Control 
              required
              name='street'
              value={address.street}
              onChange={e=>updateInput(e)}
              type={'text'} placeholder={'street'}></Form.Control>
            </Form.Group>
            <Form.Group className='mb-2'>
              <Form.Control
              required
              name='city'
              value={address.city}
              onChange={e=>updateInput(e)}
               type={'text'} placeholder={'city'}></Form.Control>
            </Form.Group>
            <Form.Group className='mb-2'>
              <Form.Control 
              required
              name='state'
              value={address.state}
              onChange={e=>updateInput(e)}
              type={'text'} placeholder={'state'}></Form.Control>
            </Form.Group>
            <Form.Group className='mb-2'>
              <Form.Control 
              required
              name='country'
              value={address.country}
              onChange={e=>updateInput(e)}
              type={'text'} placeholder={'country'}></Form.Control>
            </Form.Group>
            <Form.Group className='mb-2'>
              <Form.Control required
              name='pincode'
              value={address.pincode}
              onChange={e=>{updateInput(e)}}
              type={'text'} placeholder={'pincode'}></Form.Control>
            </Form.Group>
            <Button variant={'success'} type='submit' className='me-2'>update</Button>
            <Link to={'/users/profile'} className='btn btn-dark '>cancel</Link>
          </Form>
          </Col>
        </Row>
      </Container>
    </div>
  )
}

export default EditShippingAddress
