import React, { useState } from 'react'
import MainNavbar from '../../../layouts/pages/Navbar/MainNavbar'
import Layoutheading from '../../../layouts/components/Layoutheading/Layoutheading'
import { Container } from 'react-bootstrap'
import { Col, Row } from 'react-bootstrap'
import { Form } from 'react-bootstrap'
import { Button } from 'react-bootstrap'
import { Link, useNavigate } from 'react-router-dom'
import { Userview } from '../../models/Userview'
import * as userReducer from "../../../../Redux/users/user.reducer"
import * as useractions from "../../../../Redux/users/user.actions"
import { Appdispatch, RootState, useAppDispatch } from '../../../../Redux/store'
import { useSelector } from 'react-redux'
import SpinnerUI from '../../../ui/components/SpinnerUI/SpinnerUI'
const UserRegister = () => {
  const navigate =useNavigate();
  const dispatch:Appdispatch= useAppDispatch();
 

  const [validated, setValidated] = useState(false);

  //get the data from store
  const userState:userReducer.IntialState = useSelector((state:RootState)=>{
    return state[userReducer.userFeatureKey]
  })
  const {loading} =userState

  const [user,setUser]= useState<Userview>({
    username:"",
    email:"",
    password:""
  })
  
  const updateInput=(event:React.ChangeEvent<HTMLInputElement>)=>{
    setUser({
      ...user,
      [event.target.name]:event.target.value
    })
  }
  const handleSubmit = (event:React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const form = event.currentTarget;
    console.log("user",user)
    if(form.checkValidity() === true){
    dispatch(useractions.registerUserAction({user:user})).then((response:any)=>{
      console.log("user",user)
      if(response && !response.error){
        console.log("useresponse",user)
          navigate('/users/login')
      };
      
    
    })
    }
    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    }

    setValidated(true);
  };

  return (
    <div>
     
       <div>
        {loading && <SpinnerUI/>}
      <MainNavbar/>
      <Layoutheading heading={"Register Here"} icon={"bi-person-lock"}/>
      <pre>{JSON.stringify(user)}</pre>
      <Container>
        <Row>
          <Col xs={4}>
                <Form noValidate validated={validated} onSubmit={handleSubmit}>
                <Form.Group className="mb-2">
                          <Form.Control 
                          required
                          type="uername"
                          pattern={"[a-zA-Z0-9]{4,10}"}
                          value={user.username}
                          name={'username'} placeholder="username" onChange={updateInput}/>
                          <Form.Control.Feedback type='invalid'>choose valid username</Form.Control.Feedback>
                  </Form.Group>
                  <Form.Group className="mb-2">
                          <Form.Control
                          required
                           type="email" 
                           value={user.email}
                           name={'email'}placeholder="email"onChange={updateInput}/>
                           <Form.Control.Feedback type='invalid'>choose valid email</Form.Control.Feedback>
                  </Form.Group>
                 
                  <Form.Group className="mb-2">
                          <Form.Control
                          required
                         
                           type="password"
                           value={user.password}
                           name={'password'} placeholder="password"onChange={updateInput}/>
                           <Form.Control.Feedback type='invalid'>choose valid </Form.Control.Feedback>
                  </Form.Group>
                  <Button variant="success" type="submit">
                    Register
                  </Button>
                  <Link to={'/'} className="btn btn-dark ms-2">Cancel</Link>
                  
                </Form>
                <small className='text-success'>
                  have you already registered?<Link to={'/users/login'} className='fw-bold text-primary text-decoration-none'>Login</Link>
                </small>

          </Col>
        </Row>
      </Container>
    
    </div>
    </div>
  )
}

export default UserRegister
