import { createSlice, isRejectedWithValue, SerializedError } from "@reduxjs/toolkit";
import { CartNewResponseView, TheProduct } from "../../Modules/cart/models/CartResponseView";
import * as cartactions from "../carts/cart.actions";
import { toastUtil } from "../../Util/ToastUtil";
import { CartReduxService } from "./cartReduxService";
import { UserObj } from "../../Modules/products/models/ProductResponseView";
export const cartFeatureKey ="cartFeature";

export interface IntialState{
    loading:boolean;
    errorMessage:SerializedError;
   cartproduct:CartNewResponseView;
  
  }
const initialState:IntialState =  {
      loading:false,
      errorMessage:{} as SerializedError,
      cartproduct:{
         
               _id: '',
              products:[] as TheProduct[],
              tax:"",
              total:"",
              grandtotal:"",
               userObj: {} as UserObj,
              createdAt:"",
              updatedAt:""
            
      } as CartNewResponseView,
     
}
export const cartSlice = createSlice({
    name:"cartSlice",
    initialState:initialState,
    reducers:{
      addToCartAction:(state,action)=>{
         const{product,count}=action.payload;
          state.cartproduct = CartReduxService.addToCartUtil(state.cartproduct,product,count)
      },
      deleteToCartItemAction:(state,action)=>{
         const{productId}=action.payload;
         state.cartproduct = CartReduxService.deleteProductItemUtil(state.cartproduct,productId)
      },
      increProductQtyAction:(state,action)=>{
         const{productId}=action.payload;
         state.cartproduct = CartReduxService.incrementCartUtil(state.cartproduct,productId)

      },
      decreProductQtyAction:(state,action)=>{
         const{productId}=action.payload;
         state.cartproduct = CartReduxService.decrementCartUtil(state.cartproduct,productId)


      },
      clearCartAction:(state)=>{
       state.cartproduct={
         
            _id: '',
           products:[] as TheProduct[],
           tax:"",
           total:"",
           grandtotal:"",
            userObj: {} as UserObj,
           createdAt:"",
           updatedAt:""
         
   } as CartNewResponseView

      }
    },
    extraReducers:(builder)=>{

        /**create cart */

        builder.addCase(cartactions.createCartAction.pending,(state,action)=>{
            state.loading = true;
         }).addCase(cartactions.createCartAction.fulfilled,(state,action)=>{
            state.loading =false;
            state.cartproduct=action.payload.data;
            toastUtil.displaySuccessMessage(action.payload.msg)
         }).addCase(cartactions.createCartAction.rejected,(state,action)=>{
            state.loading=false;
            toastUtil.displayErrorMessage("cart creation is failed")
            if(isRejectedWithValue(action)){
                state.errorMessage =action.error
            }
         })

         /**get cart info */
          builder.addCase(cartactions.getCartInfoAction.pending,(state)=>{
            state.loading = true;
         }).addCase(cartactions.getCartInfoAction.fulfilled,(state,action)=>{
            state.loading =false;
            
            state.cartproduct={
               _id:action.payload.data._id,
             
               products:CartReduxService.convertProductNewResponseView(action.payload.data.products),
               total:action.payload.data.total,
               tax:action.payload.data.tax,
               grandtotal:action.payload.data.grandTotal,
               userObj:action.payload.data.userObj,
               createdAt: action.payload.data.createdAt,
               updatedAt:action.payload.data.updatedAt
            }
            console.log("cart",action.payload.data.products)
            
         }).addCase(cartactions.getCartInfoAction.rejected,(state,action)=>{
            state.loading=false;
            toastUtil.displayErrorMessage("unable to find the cart Info")
            if(isRejectedWithValue(action)){
                state.errorMessage =action.error
            }
         })

        
    }
})
export const {
   addToCartAction,
   deleteToCartItemAction,
   decreProductQtyAction,
   increProductQtyAction,
   clearCartAction
} =cartSlice.actions;