import React, { useEffect, useRef, useState } from 'react'
import MainNavbar from '../../../layouts/pages/Navbar/MainNavbar'
import Layoutheading from '../../../layouts/components/Layoutheading/Layoutheading'
import { Appdispatch, RootState, useAppDispatch } from '../../../../Redux/store'
import * as categoryReducer from "../../../../Redux/categories/category.reducer"
import * as categoryactions from "../../../../Redux/categories/category.actions"
import { useSelector } from 'react-redux'
import SpinnerUI from '../../../ui/components/SpinnerUI/SpinnerUI'
import { Container } from 'react-bootstrap'
import { Col, Row } from 'react-bootstrap'
import { Form } from 'react-bootstrap'
import { Button } from 'react-bootstrap'
import { Link, useNavigate } from 'react-router-dom'
import { CategoryResponseView, SubCategoriesView } from '../../../categories/models/CategoryResponseView'
import { ProductView } from '../../models/ProductView'
import { ProductRequestView } from '../../models/ProductRequestView'
import { UploadImageWidget } from '../../../../Util/UploadImageWidget'
import * as productReducer from "../../../../Redux/products/product.reducer"
import * as productactions from "../../../../Redux/products/product.actions"

const Uploadproducts = () => {
  const dispatch:Appdispatch = useAppDispatch();
   const cloudinaryRef = useRef<any>()
          const widgetRef = useRef<any>()
          const navigate = useNavigate();
          

  //get the data from redux
  const categoryReduxState:categoryReducer.IntialState = useSelector((state:RootState)=>{
    return state[categoryReducer.categoryFeatureKey]
  })
  const{loading,categories} = categoryReduxState;

  const [validated,setValidated]= useState<boolean>(false)
  const[subCategories,setSubCategories]= useState<SubCategoriesView[]>([] as SubCategoriesView[]);
  const[product,setProduct] = useState<ProductRequestView>({
      
    title: "",
    description: "",
    categoryId:"",
    subCategoryId:"",
    imageurl: "",
    brand: "",
    price: "",
    quantity: "",
  })
      

  useEffect(()=>{
    dispatch(categoryactions.getAllCategoriesAction())
    //uploading image cloudinary
    UploadImageWidget.upload(cloudinaryRef,widgetRef).then((imageurl)=>{
        if(imageurl){
          setProduct({...product,imageurl:imageurl.toString()})
        
        }
    }).catch((errorMessage)=>{
          console.log(errorMessage);
    })
  },[])
   //change Handler on selectcategory
  
     const SelectCategory=(event:React.ChangeEvent<HTMLSelectElement|any>)=>{
       setProduct({
        ...product,
        categoryId:event.target.value
      })
        if(categories.length>0){
          let category:CategoryResponseView| undefined = categories.find(item =>item._id === event.target.value)
          if(category && category.subCategories){
            setSubCategories(category.subCategories)
          }else{
            setSubCategories([] as SubCategoriesView[])
          }
        }
     }
     const productHandler=(event:React.ChangeEvent<HTMLInputElement|any>)=>{
      setProduct({
        ...product,
        [event.target.name]:event.target.value
      })
     }
     const clickEditImage=()=>{
      widgetRef.current.open();
  
    }
    //Form submition for subcategories
    const handleSubmit = (event:React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const form = event.currentTarget;
        
        if(form.checkValidity() === true){
        dispatch(productactions.createProductAction({product:product})).then((response:any)=>{
          
          if(response && !response.error){
            
           navigate("/products/fashion")
          };
          
        
        })
        }
        if (form.checkValidity() === false) {
          event.preventDefault();
          event.stopPropagation();
        }
    
        setValidated(true);
      };
  return (
    <div>
      {loading && <SpinnerUI/>}
      <MainNavbar/>
      <Layoutheading heading={"uploadproducts"}/>
      <pre>{JSON.stringify(product)}</pre>
      <Container>
        <Row>
          <Col xs={5}>
          <Form  noValidate validated={validated} onSubmit={handleSubmit}>

            <Form.Group className='mb-2'>
              <Button variant={"warning"} onClick={clickEditImage}>Upload</Button>
            </Form.Group>

            <Form.Group className='mb-2'>
              <Form.Select 
              required
              value={product.categoryId}
              onChange={e=>SelectCategory(e)}>
                <option>Select Category</option>
                {
                  categories && categories.map(category=>{
                    return(
                      <option value={category._id}>{category.name}</option>
                    )

                  })
                }
                </Form.Select>
            </Form.Group>

            <Form.Group className='mb-2'>
              <Form.Select 
              required
              value={product.subCategoryId}
              name='subCategoryId'
              onChange={e=>productHandler(e)}>
                <option>Select SubCategory</option>
                {
                  subCategories && subCategories.map(subcategory=>{
                    return(
                      <option value={subcategory._id}>{subcategory.name}</option>
                    )

                  })
                }
                </Form.Select>
            </Form.Group>

            <Form.Group className='mb-2'>
              <Form.Control 
              required
              value={product.title}
              name='title'
              onChange={e=>productHandler(e)}
              type='text' placeholder='title'>
              </Form.Control>
            </Form.Group>
            <Form.Group className='mb-2'>
              <Form.Control 
                  required
                 value={product.description}
                 name='description'
                 onChange={e=>productHandler(e)}
               as='textarea' placeholder='Description'>
              </Form.Control>
            </Form.Group>
            <Form.Group className='mb-2'>
              <Form.Control 
              required
                 value={product.brand}
                 name='brand'
                 onChange={e=>productHandler(e)}
               type='text' placeholder='Brand'>
              </Form.Control>
            </Form.Group>
            <Form.Group className='mb-2'>
              <Form.Control 
              required
                 value={product.price}
                 name='price'
                 onChange={e=>productHandler(e)}
               type='number' placeholder='Price'>
              </Form.Control>
            </Form.Group>
            <Form.Group className='mb-2'>
              <Form.Control 
              required
                 value={product.quantity}
                 name='quantity'
                 onChange={e=>productHandler(e)}
               type='number' placeholder='Quantity'>
              </Form.Control>
            </Form.Group>
            <Form.Group className='mb-2 '>
             <Button 
              type ={'submit'} variant={'success'} className='me-2'>Upload</Button>
             <Link to={'/'} className='btn btn-danger '>Cancel</Link>
            </Form.Group>
           
            
          </Form>
          </Col>
          <Col xs={3}>
          <img src={product.imageurl} alt="/"className='img-fluid'></img>
          </Col>
        </Row>
      </Container>
    </div>
  )
}

export default Uploadproducts
