  import React, { useState } from 'react'
  import { ListGroup, Offcanvas } from 'react-bootstrap';
  import { Button,Form } from 'react-bootstrap';
  import { SubCategoriesView } from '../../categories/models/CategoryResponseView';

  export interface IProps{
  subCategories:SubCategoriesView[];
  filteredTheProducts:(subCategories:SubCategoriesView[]) => void;
  setSubCategories:React.Dispatch<React.SetStateAction<SubCategoriesView[]>>
  }
  /*
  The Product sidebar component
  @param subcategories
  @param  filteredTheProducts
  @param setSubCategories
  @constructor
  */

  const ProductSideBar:React.FC<IProps> = ({subCategories,filteredTheProducts,setSubCategories}) => {
    const[show,setShow] = useState(false);
    const handleClose =() => setShow(false);
    const handleShow =()=> setShow(true);

    /**
     * on click of apply button
     */
    const clickApply =()=>{
      setShow(false);
      
      console.log("subcategories at Apply ", subCategories);
      filteredTheProducts(subCategories)
   
    }

    /**
     * to update checks from nested 
     * @param event
     * @param id
     */
    const updateChecks =(event:any,id:string | undefined)=>{
        if(id){
         setSubCategories( subCategories.map(item=>{
          
          if(item._id === id){
            return{
              ...item,
              isChecked:event.target.checked
                          }
          }
          return item
        }))
        }
    }
    
    return (
      <div>
          <Button variant="success" className="m-2 filter-button" onClick={handleShow}>
          <i className='bi bi-sliders'></i>
        </Button>

      

        <Offcanvas show={show} onHide={handleClose} placement={'start'} style={{width:"300px"}}>
          <Offcanvas.Header closeButton className='bg-success text-white'>
            <Offcanvas.Title>Search Filters Here</Offcanvas.Title>
          </Offcanvas.Header>
          <Offcanvas.Body className='bg-light-success text-white '>
        
            {
              
              subCategories && Array.isArray(subCategories) && subCategories.length >0 &&
              <ListGroup>
                {
                  subCategories && subCategories.map((subCategory:any) => {
                    return(
                      <ListGroup.Item key={subCategory._id}>
                        <Form.Check 
                                    checked={subCategory.isChecked}
                                    onChange={e => updateChecks(e,subCategory._id)}
                                    
                                    type= {'checkbox'}
                                    label={subCategory.name}>

                        </Form.Check>
                      </ListGroup.Item>

                    )  
                  })
                }
              </ListGroup>
            }
            <Button variant='success' className='m-2' onClick={clickApply}>Apply</Button>
          </Offcanvas.Body>
        </Offcanvas>
      </div>
    )
  }

  export default ProductSideBar
