import { createAsyncThunk } from "@reduxjs/toolkit";
import { CartRequestView } from "../../Modules/cart/models/CartRequestView";
import { CartService } from "../../Modules/cart/services/CartService";
import { CartResponseView } from "../../Modules/cart/models/CartResponseView";
import { AuthUtil } from "../../Util/AuthUtil";

   /**
 * @usage: create a cart
 * @url: http://localhost:9000/api/carts/
 * @params: products[{product,count,price}],total,tax,grandtotal
 * @method: post
 * @access: private
 */
  export const createCartAction:any = createAsyncThunk('users/createCartAction',
    async(payload:{cart:CartRequestView},{rejectWithValue}):Promise<{msg:string,data:CartResponseView,status:string}|any>=>{
      try{
        if(AuthUtil.isSetTokenToRequestHeader()){
        const{cart}=payload
        let response= await CartService.createCart(cart);
        return response.data;
        }
      }catch(err:any){
           if(!err.response){
            throw err
           }
           return rejectWithValue(err.response.data);
      }
    })
 /**
* @usage: get cart info
* @url: http://localhost:9000/cart/me
* @params: no-params
* @method: get
* @access: private
*/
export const getCartInfoAction:any = createAsyncThunk('users/getCartInfoAction',
    async(payload:{},{rejectWithValue}):Promise<{msg:string,data:CartResponseView,status:string}|any>=>{
      try{
        if(AuthUtil.isSetTokenToRequestHeader()){

        let response= await CartService.getCartInfo();
        return response.data;
        }
      }catch(err:any){
           if(!err.response){
            throw err
           }
           return rejectWithValue(err.response.data);
      }
    })