import { bindActionCreators, createSlice, isRejectedWithValue, SerializedError } from "@reduxjs/toolkit";
import { Userview } from "../../Modules/users/models/Userview";
import * as useractions from "../users/user.actions";
import { toastUtil } from "../../Util/ToastUtil";
import { TokenUtil } from "../../Util/TokenUtil";
import { AddressView } from "../../Modules/users/models/AddressView";

export const userFeatureKey ="userFeature";

export interface IntialState{
    loading:boolean;
    errorMessage:SerializedError;
    user:Userview;
    token:string;
    isAuthenticated:boolean;
    address:AddressView;
}
const initialState:IntialState =  {
      loading:false,
      errorMessage:{} as SerializedError,
      user:{} as Userview,
      token:"",
      isAuthenticated:false,
      address:{} as AddressView
}
export const userSlice = createSlice({
    name:"userSlice",
    initialState:initialState,
    reducers:{
      userLogoutAction:(state)=>{
         state.user = {} as Userview;
         state.isAuthenticated=false;
         TokenUtil.removeTokenFromSession();//save token session
         toastUtil.displaySuccessMessage("Logout is success");
      }

    },
    extraReducers:(builder)=>{
      /**
       * Register User
       */
         builder.addCase(useractions.registerUserAction.pending,(state,action)=>{
            state.loading = true;
           // console.log("userreducer",action.payload)

         }).addCase(useractions.registerUserAction.fulfilled,(state,action)=>{
            state.loading =false;
            state.user=action.payload;
         // console.log("userreducer",action.payload)
            toastUtil.displaySuccessMessage("Registation is success")
         }).addCase(useractions.registerUserAction.rejected,(state,action)=>{
            state.loading=false;
            toastUtil.displayErrorMessage(action.payload.msg)
            if(isRejectedWithValue(action)){
                state.errorMessage =action.payload.msg
              // console.log("actionerror",action.error)
             //console.log("userreducer",action.payload)
               
            }
         })
    
         /**Login user */

    builder.addCase(useractions.loginUserAction.pending,(state,action)=>{
        state.loading = true;
     }).addCase(useractions.loginUserAction.fulfilled,(state,action)=>{
        state.loading =false;
        state.user = action.payload.data;
        state.isAuthenticated=true;
        TokenUtil.saveTokenToSession(action.payload.token);//save token session
        toastUtil.displaySuccessMessage(action.payload.msg);
     }).addCase(useractions.loginUserAction.rejected,(state,action)=>{
        state.loading=false;
        state.isAuthenticated=false;
        state.user ={} as Userview;
        TokenUtil.removeTokenFromSession();//remove token from session storage
        toastUtil.displayErrorMessage(action.payload.msg)
        if(isRejectedWithValue(action)){
            state.errorMessage =action.error
        }
     })

     /**Get User Info */
     builder.addCase(useractions.getUserInfoAction.pending,(state)=>{
      state.loading=true;
     }).addCase(useractions.getUserInfoAction.fulfilled,(state,action)=>{
      state.loading=false;
      state.isAuthenticated=true;
      console.log("userbeforereducerdata")
      state.user= action.payload.data;
      console.log("userReducer")
     
     }).addCase(useractions.getUserInfoAction.rejected,(state,action)=>{
      state.loading=false;
      state.user= {} as Userview;
      state.isAuthenticated=false;
      toastUtil.displayErrorMessage("unable to get user info");
     if(isRejectedWithValue(action)){
     
      state.errorMessage=action.payload.msg;
      
     } 
     })

     /**Update Profile Picture */

     builder.addCase(useractions.updateProfilePicAction.pending,(state,action)=>{
        state.loading = true;
      
     }).addCase(useractions.updateProfilePicAction.fulfilled,(state,action)=>{
        state.loading = false;
        console.log("userreducer",action.payload.data)
        state.user = action.payload.data;
        console.log("userreducer",action.payload.data)
        toastUtil.displaySuccessMessage(action.payload.msg);
     }).addCase(useractions.updateProfilePicAction.rejected,(state,action)=>{
        state.loading=false;
        state.user ={} as Userview;
         toastUtil.displayErrorMessage("update profile pic is failed")
        if(isRejectedWithValue(action)){
            state.errorMessage = action.error
        }
     })
      /**change password*/

      builder.addCase(useractions.changepasswordAction.pending,(state,action)=>{
        state.loading = true;
     }).addCase(useractions.changepasswordAction.fulfilled,(state,action)=>{
        state.loading =false;
        state.user = action.payload.data;
        toastUtil.displaySuccessMessage(action.payload.msg);
     }).addCase(useractions.changepasswordAction.rejected,(state,action)=>{
        state.loading=false;
        state.user ={} as Userview;
         toastUtil.displayErrorMessage("change password is failed")
        if(isRejectedWithValue(action)){
            state.errorMessage =action.error
        }
     })

      /**create new address*/

      builder.addCase(useractions.createNewAddressAction.pending,(state,action)=>{
        state.loading = true;
     }).addCase(useractions.createNewAddressAction.fulfilled,(state,action)=>{
        state.loading =false;
        state.address = action.payload.data;
        toastUtil.displaySuccessMessage(action.payload.msg);
     }).addCase(useractions.createNewAddressAction.rejected,(state,action)=>{
        state.loading=false;
         toastUtil.displayErrorMessage("address creation is failed")
        if(isRejectedWithValue(action)){
            state.errorMessage =action.error
        }
     })
      /**update address*/

      builder.addCase(useractions.updateAddressAction.pending,(state,action)=>{
        state.loading = true;
     }).addCase(useractions.updateAddressAction.fulfilled,(state,action)=>{
        state.loading =false;
        state.address = action.payload;
        toastUtil.displaySuccessMessage(action.payload.msg);
     }).addCase(useractions.updateAddressAction.rejected,(state,action)=>{
        state.loading=false;
         toastUtil.displayErrorMessage("update address is failed")
        if(isRejectedWithValue(action)){
            state.errorMessage =action.payload.msg;
        }
     })
      /**get address*/

      builder.addCase(useractions.getAddressAction.pending,(state,action)=>{
        state.loading = true;
     }).addCase(useractions.getAddressAction.fulfilled,(state,action)=>{
        state.loading =false;
        state.address = action.payload.data;
       
     }).addCase(useractions.getAddressAction.rejected,(state,action)=>{
        state.loading=false;
        
        if(isRejectedWithValue(action)){
            state.errorMessage =action.payload.msg
        }
     })
     /**delete address*/

     builder.addCase(useractions.DeleteAddressAction.pending,(state,action)=>{
        state.loading = true;
     }).addCase(useractions.DeleteAddressAction.fulfilled,(state,action)=>{
        state.loading =false;
        state.address = {} as AddressView;
        toastUtil.displayInfoMessage(action.payload.msg);
     }).addCase(useractions.DeleteAddressAction.rejected,(state,action)=>{
        state.loading=false;
         toastUtil.displayErrorMessage("deleting address is failed")
        if(isRejectedWithValue(bindActionCreators)){
            state.errorMessage =action.error
        }
     })
    }
    
})
export const {userLogoutAction}=userSlice.actions;