import { createAsyncThunk } from "@reduxjs/toolkit";
import { CategoryRequestView } from "../../Modules/categories/models/CategoryRequestView";
import { CategoryService } from "../../Modules/categories/services/CategoryService";
import { CategoryResponseView } from "../../Modules/categories/models/CategoryResponseView";
import { AuthUtil } from "../../Util/AuthUtil";

  /**
 * @usage: create category
 * @url: http://localhost:9000/categories/
 * @params: name,description
 * @method: post
 * @access: private
 */
  export const createCategoryAction:any = createAsyncThunk('users/createCategoryAction',
    async(payload:{category:CategoryRequestView},{rejectWithValue}):Promise<{status:string,data:CategoryResponseView,msg:string}|any>=>{
      try{
        if(AuthUtil.isSetTokenToRequestHeader()){
        const{category} =payload;
        let response= await CategoryService.createCategory(category);
        return response.data;
        }
      }catch(err:any){
           if(!err.response){
            throw err
           }
           return rejectWithValue(err.response.data);
      }
    })
     /**
 * @usage: create sub category
 * @url: http://localhost:9000/categories/:categoryId
 * @params: name,description
 * @method: post
 * @access: private
 */
     export const createSubCategoryAction:any = createAsyncThunk('users/createSubCategoryAction',
        async(payload:{category:CategoryRequestView,categoryId:string},{rejectWithValue}):Promise<{status:string,msg:string,data:CategoryResponseView}|any>=>{
          try{
            if(AuthUtil.isSetTokenToRequestHeader()){
            const{category,categoryId} =payload;
            let response= await CategoryService.createSubCategory(category,categoryId);
            return response.data;
            }
          }catch(err:any){
               if(!err.response){
                throw err
               }
               return rejectWithValue(err.response.data);
          }
        })
        /**
 * @usage: get all categories
 * @url: http://localhost:9000/api/categories/
 * @params: no-params
 * @method: get
 * @access: public
 */
        export const getAllCategoriesAction:any = createAsyncThunk('users/getAllCategoriesAction',
            async(payload:{},{rejectWithValue}):Promise<{status:string,msg:string,data:CategoryResponseView}|any>=>{
              try{
            
                let response= await CategoryService.getAllCategories();
                return response.data;
              }catch(err:any){
                   if(!err.response){
                    throw err
                   }
                   return rejectWithValue(err.response.data);
              }
            })
