import React, { useEffect } from 'react'
import MainNavbar from '../../../layouts/pages/Navbar/MainNavbar'
import Layoutheading from '../../../layouts/components/Layoutheading/Layoutheading'
import { Appdispatch, RootState, useAppDispatch } from '../../../../Redux/store'
import * as orderactions from "../../../../Redux/orders/orders.actions"
import * as orderReducer from "../../../../Redux/orders/orders.reducer";
import { useSelector } from 'react-redux'
import SpinnerUI from '../../../ui/components/SpinnerUI/SpinnerUI'
import { Badge, Col, Container, ListGroup, ListGroupItem, Row, Table } from 'react-bootstrap'
import { Link } from 'react-router-dom'

const MyOrders = () => {
  const dispatch:Appdispatch = useAppDispatch();
   
  //get order state from redux

  const orderReduxState:orderReducer.IntialState = useSelector((State:RootState)=>{
    return State[orderReducer.orderFeatureKey]
  })
  const{orderproducts,loading} = orderReduxState;

  useEffect(()=>{
     dispatch(orderactions.getmyOrderAction())
  },[])


  
  return (
    <div>
      
      {loading && <SpinnerUI/>}
      <MainNavbar/>
      <Layoutheading heading={"MyOrders"} icon={"bi-check-all   "}/>
      
     {
      orderproducts && Object.keys(orderproducts).length>0 &&
      <Container>
        <Row>
          <Col>
          <Table striped hover className='text-center shadow-lg'>
            <thead className='table-success text-white'>
              <tr>
                <th>Order Number</th>
                <th>Order Details</th>
                <th>Order Placed On</th>
                <th>Order By</th>
                <th>Total Mount</th>
                <th>Payment Type</th>
                <th>Order Status</th>
              </tr>
            </thead>
            <tbody className='table-body'>
              {
                orderproducts.map(order =>{
                  return (
                    <tr key={order._id}>
                      <td>{order._id}</td>
                      <td>
                        <ListGroup>
                          {
                            order.products && order.products.map(item =>{
                              return(
                                <ListGroupItem key={item._id}>
                                  <Row>
                                    <Col xs={2}>
                                    <img  src={item?.imageurl} width={25} height={25} alt='/'></img>
                                    </Col>
                                    <Col>
                                    <Link className='text-decoration-none text-success' to={`products/view/fashion/${item._id}`}>{item.title}</Link>
                                    </Col>
                                  </Row>

                                </ListGroupItem>
                              )
                            })
                          }
                        </ListGroup>
                      </td>
                      <td>{new Date(order.createdAt).toLocaleDateString()} {new Date(order.createdAt).toLocaleTimeString()}</td>
                      <td>{order.orderBy?.username}</td>
                      <td>&#8377;{Number(order.grandtotal).toFixed(2)}</td>
                      <td>{order.paymentType}</td>
                      <td className='fw-bold'>
                       
                        {  
                        order.orderstatus === "Order Placed"  && 
                        <Badge bg='info'>{order.orderstatus}</Badge>
                        }
                         {  
                        order.orderstatus === "Dispatched"  &&
                        <Badge bg='primary'>{order.orderstatus}</Badge>
                        }
                         {  
                        order.orderstatus === "Processing"  &&
                        <Badge bg='success'>{order.orderstatus}</Badge>
                        }
                         {  
                        order.orderstatus === "Delivered"  &&
                        <Badge bg='warning'>{order.orderstatus}</Badge>
                        }
                         {  
                        order.orderstatus === "Cancelled"  &&
                        <Badge bg='danger'>{order.orderstatus}</Badge>
                        }
                         {  
                        order.orderstatus === "Completed"  &&
                        <Badge bg='success'>{order.orderstatus}</Badge>
                        }
                        </td>
                    </tr>
                  )
                })
              }
            </tbody>
          </Table>
          </Col>
        </Row>
      </Container>
     } 

    </div>
  )
}

export default MyOrders
