/**
 * @usage: Create new address
 * @url: http://localhost:9000/api/addresses/new
 * @params:mobile,flat,landmark,street,city,state,country,pincode
 * @method: post
 * @access: ptivate
 */
export const createNewAddressAction:any = createAsyncThunk('users/createNewAddressAction',
    async(payload:{address:AddressView},{rejectWithValue}):Promise<{status:string,msg:string,data:AddressView}|any>=>{
      try{
        if(AuthUtil.isSetTokenToRequestHeader()){
        const {address}= payload;
        let response= await UserService.createNewAddress(address);
        return response.data;
        }
      }catch(err:any){
           if(!err.response){
            throw err
           }
           return rejectWithValue(err.response.data);
      }
    })
    /**
 * @usage: Delete address
 * @url: http://localhost:9000/api/addresses/:addressId
 * @params: Name,email,mobile,address,landmark,street,city,state,country,pincode
 * @method: put
 * @access: ptivate
 */  
    export const DeleteAddressAction:any = createAsyncThunk('users/DeleteAddressAction',
        async(payload:{addressId:string},{rejectWithValue}):Promise<{status:string,data:AddressView,msg:string}|any>=>{
          try{
            if(AuthUtil.isSetTokenToRequestHeader()){
            const {addressId}= payload;
            let response= await UserService.DeleteAddress(addressId);
            return response.data;
            }
          }catch(err:any){
               if(!err.response){
                throw err
               }
               return rejectWithValue(err.response.data);
          }
        })
        /**
 * @usage: update address
 * @url: http://localhost:9000/api/addresses/:addressId
 * @params:mobile,flat,landmark,street,city,state,country,pincode
 * @method: put
 * @access: ptivate
 */
        export const updateAddressAction:any = createAsyncThunk('users/updateAddressAction',
            async(payload:{address:AddressView,addressId:string},{rejectWithValue}):Promise<{data:AddressView,msg:string,status:string}|any>=>{
              try{
                if(AuthUtil.isSetTokenToRequestHeader()){
                const {address,addressId}= payload;
                let response= await UserService.updateAddress(address,addressId);
                return response.data;
                }
              }catch(err:any){
                   if(!err.response){
                    throw err
                   }
                   return rejectWithValue(err.response.data);
              }
            })
