import React from 'react'
import { Nav, Navbar, NavDropdown, NavLink } from 'react-bootstrap'
import { Container } from 'react-bootstrap'
import { Link, useNavigate } from 'react-router-dom'
import { Appdispatch, RootState, useAppDispatch } from '../../../../Redux/store'
import * as userReducer from "../../../../Redux/users/user.reducer"
import * as useractions from "../../../../Redux/users/user.actions"
import { useSelector } from 'react-redux'
import { TokenUtil } from '../../../../Util/TokenUtil'
import * as cartReducer from "../../../../Redux/carts/cart.reducer"
import * as cartActions from "../../../../Redux/carts/cart.actions"

const MainNavbar = () => {
  const navigate = useNavigate();
  const dispatch:Appdispatch= useAppDispatch()

   //get data from store
  const userState:userReducer.IntialState= useSelector((state:RootState)=>{
       return state[userReducer.userFeatureKey]
  })
  //get cart info from Redux
  const cartReduxState:cartReducer.IntialState =useSelector((state:RootState)=>{
    return state[cartReducer.cartFeatureKey]
  })
  const {cartproduct}= cartReduxState;
  const {user,isAuthenticated} =userState;

  const navigateTo = (path:string):void =>{
    navigate(path);
  }

  const clickLogout=()=>{
    dispatch({

      type: `${userReducer.userLogoutAction}`
    })
    navigateTo("/")
  }
  return (
    <div>

      <Navbar expand="sm" bg="success" variant="dark">
            <Container>
                    <Navbar.Brand>
                      <Link to={"/"} className='text-white text-decoration-none'>React E-Commerce</Link>
                    </Navbar.Brand>
                    <Navbar.Collapse id='basic-navbar-nav'>

                      {/*all are private routes*/}

                    {
                             TokenUtil.IsLoggedIn() && isAuthenticated && user && Object.keys(user).length>0 &&
                             <>
                      <Nav className=''>
                        <Link to={'/products/fashion'} className ='nav-link'>Fashion</Link>
                      </Nav>
                      <Nav className=''>
                        <Link to={'/products/electronics'} className ='nav-link'>Electronics</Link>
                      </Nav>
                      <Nav className=''>
                        <Link to={'/products/household'} className ='nav-link'>HouseHold</Link>
                      </Nav>

                     <Nav>
                    {
                      TokenUtil.IsLoggedIn() && user.isAdmin &&
                      <NavDropdown title="Admin" id="basic-nav-dropdown">
                      <NavDropdown.Item onClick={()=> navigate("/categories/add")}>Add Categories</NavDropdown.Item>
                      <NavDropdown.Item onClick={()=> navigate("/products/upload")}> Upload Products</NavDropdown.Item>
                     
                     {
                      user.isSuperAdmin && 
                      <>
                        <NavDropdown.Divider />
                      <NavDropdown.Item onClick={()=> navigate("/products/admin")}>Manage Products</NavDropdown.Item>
                      <NavDropdown.Item onClick={()=> navigate("/orders/admin")}>Manage Orders</NavDropdown.Item>
                      </>
                     }
                    </NavDropdown>
                    }
                     </Nav>
                     </>
                  }

                    
                      
                         {
                             TokenUtil.IsLoggedIn() && isAuthenticated && user && Object.keys(user).length>0 &&
                             <>
                              <div className='d-flex ms-auto'>
                                <Nav className=''>
                             <Link to={'/cart/list'} className='nav-link pe-2'>
                             <i className='bi bi-cart-fill'>
                               <span className='cart-count'>{cartproduct?.products.length}</span>
                             </i>
                             </Link>
                           </Nav>
                          
                          </div>
                                 <Nav className=''>
                        <Link to={'/products/fashion'} className='nav-link'>
                          <img src={user.imageurl} alt="" width={25} height={25} className='rounded-circle'></img>
                        </Link>
                      </Nav>
                                  <Nav>
                                  <NavDropdown title={user.username} id="basic-nav-dropdown">
                            <NavDropdown.Item onClick={()=> navigate("/users/profile")}>Profile</NavDropdown.Item>
                            <NavDropdown.Item onClick={()=> navigate("/users/changepassword")}>change password</NavDropdown.Item>
                            <NavDropdown.Item onClick={()=> navigate("/users/orders/me")}>Your orders</NavDropdown.Item>
                          
                            <NavDropdown.Divider />
                            <NavDropdown.Item onClick={clickLogout}><i className='bi bi-power' ></i>Logout</NavDropdown.Item>
                          </NavDropdown>
                                  </Nav>

                             </>
                         }
                     

                    </Navbar.Collapse>
              
            </Container>
            </Navbar>
    </div>
  )
}

export default MainNavbar
