import React from 'react'
import { Link } from 'react-router-dom'
import { TokenUtil } from '../../../../Util/TokenUtil'
import * as userReducer from "../../../../Redux/users/user.reducer"
import { useSelector } from 'react-redux'
import { RootState } from '../../../../Redux/store'
const Homepage:React.FC = () => {
   //get data from store
   const userState:userReducer.IntialState= useSelector((state:RootState)=>{
    return state[userReducer.userFeatureKey]
})
const {isAuthenticated} =userState;

  return (
    <div>
    <div className='landing-page'>
      <div className='wrapper'>

        <div className='d-flex flex-column justify-content-center align-items-center text-center h-100'>
          <p className='display-1 text-success'>React E-Commerce 2024</p>
          <div>
           {
                TokenUtil.IsLoggedIn() && isAuthenticated ? <Link to ={'products/fashion'} className="btn btn-warning">
                  <i className='bi bi-phone'></i>products</Link>:
                <Link to ={'users/login'} className="btn btn-success ms-2">
            <i className='bi bi-lock'></i>Login</Link>
           } 
          </div>
        </div>
      </div>
    </div>
    </div>
  )
}

export default Homepage
