import axios from "axios";
import { OrderRequestView } from "../models/OrderRequestView";
import { OrderResponseView } from "../models/OrderResponseView";

export class orderService {

    private static serverUrl :string =  process.env.REACT_APP_EXPRESS_SERVER_URL? process.env.REACT_APP_EXPRESS_SERVER_URL: "";
    /**
 * @usage: place an order
 * @url: http://localhost:9000/api/orders/
 * @params:products[{product,count,price}],total,tax,grandtotal,paymenttype
 * @method: post
 * @access: private
 */

    public static placeOrder(order:OrderRequestView):Promise<{data:{msg:string,data:OrderResponseView,status:string}}>{
        const dataUrl=`${this.serverUrl}/api/orders/place/`;
        console.log("service",order)
        return axios.post(dataUrl,order);
    } ;
    /**
 * @usage: get all  orders
 * @url: http://localhost:9000/api/orders/
 * @params: no-params
 * @method: get
 * @access: private
 */
    public static getAllOrders():Promise<{data:{msg:string,data:OrderResponseView[],status:string}}>{
        const dataUrl=`${this.serverUrl}/api/orders`;
        return axios.get(dataUrl);
    } ;
    /**
 * @usage: get my  order
 * @url: http://localhost:9000/api/orders/me
 * @params: no-params
 * @method: get
 * @access: private
 */
    public static getmyOrder():Promise<{data:{msg:string,data:OrderResponseView[],status:string}}>{
        const dataUrl=`${this.serverUrl}/api/orders/me`;
        return axios.get(dataUrl);
    } ;
    /**
 * @usage: updateorderStatus
 * @url: http://localhost:9000/api/orders/:orderId
 * @params:orderstatus
 * @method: put
 * @access: private
 */
    public static updateOrderStatus(orderstatus:string,orderId:string):Promise<{data:{msg:string,data:OrderResponseView,status:string}}>{
        const dataUrl=`${this.serverUrl}/api/orders/${orderId}`;
        return axios.put(dataUrl,{orderstatus:orderstatus});
    } ;
}
