import React, { useEffect } from 'react'
import MainNavbar from '../../../layouts/pages/Navbar/MainNavbar'
import Layoutheading from '../../../layouts/components/Layoutheading/Layoutheading'
import * as productReducer from  "../../../../Redux/products/product.reducer"
import * as productactions from "../../../../Redux/products/product.actions"
import { Appdispatch, RootState, useAppDispatch } from '../../../../Redux/store'
import { useSelector } from 'react-redux'
import { Col, Container, Row ,Table} from 'react-bootstrap'
import { Link, useNavigate } from 'react-router-dom'
import { Button } from 'react-bootstrap'

const ManageOrders = () => {
  const dispatch:Appdispatch = useAppDispatch();
  const navigate = useNavigate();

  const productReduxState:productReducer.IntialState= useSelector((State:RootState)=>{
    return State[productReducer.productFeatureKey]
  })
  const{products} =productReduxState;

  useEffect(()=>{
    dispatch(productactions.getAllProductAction())
  },[])
const DeleteHandler=(productId:string|undefined)=>{
 dispatch(productactions.deleteProductAction({productId:productId})).then((response:any)=>{
  if(response && !response.error){
    dispatch(productactions.getAllProductAction())
  }
 })
}
  return (
    <div>
      <MainNavbar/>
      <Layoutheading heading={"ManageOrders"}/>
      
      <Container>
        <Row>
          <Col>
          <Table striped hover className="text-center shadow-lg">
            <thead className='text-white'> 
              <tr>
                <th>SNo</th>
                <th>Product Id</th>
                <th>Image</th>
                <th>Brand</th>
                <th>Title</th>
                <th>Price</th>
                <th>Quantity</th>
                <th>Actions</th>
              </tr>

            </thead>
            <tbody>
              {
                products.map((product,index)=>{
                  return(
                    <tr key={product._id}>
                      <td>{index+1}</td>
                      <td>{product._id.substring(product._id.length-5).toUpperCase()}</td>
                      <td>
                        <img src={product.imageurl} alt='/' width={50} height={50}/>
                      </td>
                      <td>{product.brand}</td>
                      <td>{product.title}</td>
                      <td>&#8377;{product.price.toFixed(2)}</td>
                      <td>{product.quantity}</td>
                      <td>
                        <Link to={`/products/edit/${product._id}`}>
                        <Button variant='primary'>
                          <i className='bi bi-pencil-square'></i>
                        </Button>
                      </Link>
                     
                    
                        <Button variant='success' className='ms-2' onClick={()=>DeleteHandler(product._id)}>
                          <i className='bi bi-trash-fill'></i>
                        </Button>
                     </td>
                    </tr>
                  )
                })
              }
            </tbody>
          </Table>
          </Col>
        </Row>
      </Container>

    </div>
  )
}

export default ManageOrders
