import React, { useEffect } from 'react'
import { Button } from 'react-bootstrap'
import Toastconfig from './Modules/ui/components/Toastconfig/Toastconfig'
import { BrowserRouter, Route, Routes } from 'react-router-dom'
import Homepage from './Modules/layouts/pages/Home/Homepage'
import FashionCatelogue from './Modules/products/pages/catalogues/Fashions/FashionCatelogue'
import ElectronicCatalogue from './Modules/products/pages/catalogues/Electronics/ElectronicCatalogue'
import Householdcatelogue from './Modules/products/pages/catalogues/HouseHolds/Householdcatelogue'
import Uploadproducts from './Modules/products/pages/Upload_Products/Uploadproducts'
import Manageproducts from './Modules/products/pages/Manage_Products/Manageproducts'
import Addcategory from './Modules/categories/pages/Add_Category/Addcategory'
import ManageOrders from './Modules/orders/pages/ManageOrders/ManageOrders'
import CartPage from './Modules/cart/pages/cart_page/CartPage'
import CheckOutPage from './Modules/cart/pages/checkout_page/CheckOutPage'
import UserProfile from './Modules/users/pages/UserProfile/UserProfile'
import UserRegister from './Modules/users/pages/User_Register/UserRegister'
import UserLogin from './Modules/users/pages/User_login/UserLogin'
import Userpassword from './Modules/users/pages/User_Password/Userpassword'
import MyOrders from './Modules/orders/pages/MyOrders/MyOrders'
import Pagenotfound from './Modules/ui/components/Pagenotfound/Pagenotfound'
import * as useractions from "../src/Redux/users/user.actions"
import { Appdispatch, useAppDispatch } from './Redux/store'
import { TokenUtil } from './Util/TokenUtil'
import AddShippingAddress from './Modules/users/pages/Shipping_address/AddShippingAddress'
import EditShippingAddress from './Modules/users/pages/Shipping_address/EditShippingAddress'
import ViewProduct from './Modules/products/pages/View_Products/ViewProduct'
import OrderDetails from './Modules/orders/pages/OrderDetails/OrderDetails'
import Editproduct from './Modules/products/pages/Edit_Products/Editproduct'



const App = () => {
  const dispatch:Appdispatch =useAppDispatch();

  useEffect(()=>{
      if(TokenUtil.IsLoggedIn()){
      
        dispatch(useractions.getUserInfoAction())
      }
      
    },[])
  
  return (
    <div>
     
   
     <BrowserRouter>
     <Toastconfig/>
     <Routes>
          <Route path ={"/"} element= {<Homepage/>}/>
          <Route path ={"/products/fashion"} element= {<FashionCatelogue/>}/>
          <Route path ={"/products/electronics"} element= {<ElectronicCatalogue/>}/>
          <Route path ={"/products/household"} element= {<Householdcatelogue/>}/>
          <Route path ={"/products/upload"} element= {<Uploadproducts/>}/>
          <Route path ={"/products/view/:categoryName/:productId"} element= {<ViewProduct/>}/>
          <Route path ={"/products/edit/:productId"} element= {<Editproduct/>}/>
          <Route path ={"/products/admin"} element= {<Manageproducts/>}/>
          <Route path ={"/categories/add"} element= {<Addcategory/>}/>
          <Route path ={"/orders/admin"} element= {<ManageOrders/>}/>
          <Route path ={"/orders/details"} element= {<OrderDetails/>}/>
          <Route path ={"/cart/list"} element= {<CartPage/>}/>
          <Route path ={"/cart/checkout"} element= {<CheckOutPage/>}/>
          <Route path ={"/users/profile"} element= {<UserProfile/>}/>
          <Route path ={"/users/add-shipping-address"} element= {<AddShippingAddress/>}/>
          <Route path ={"/users/edit-shipping-address/:addressId"} element= {<EditShippingAddress/>}/>
          <Route path ={"/users/register"} element= {<UserRegister/>}/>
          <Route path ={"/users/login"} element= {<UserLogin/>}/>
          <Route path ={"/users/changepassword"} element= {<Userpassword/>}/>
          <Route path ={"/users/orders/me"} element= {<MyOrders/>}/>
          <Route path ={"*"} element= {<Pagenotfound/>}/>
          
          
     </Routes>
     </BrowserRouter>
    </div>
  )
}

export default App
