import { createAsyncThunk } from "@reduxjs/toolkit";
import { OrderResponseView } from "../../Modules/orders/models/OrderResponseView";
import { OrderRequestView } from "../../Modules/orders/models/OrderRequestView";
import { orderService } from "../../Modules/orders/services/orderService";
import { AuthUtil } from "../../Util/AuthUtil";

 /**
 * @usage: place an order
 * @url: http://localhost:9000/api/orders/
 * @params:products[{product,count,price}],total,tax,grandtotal,paymenttype
 * @method: post
 * @access: private
 * */
 export const placeOrderAction:any = createAsyncThunk('orders/placeOrderAction',
    async(payload:{order:OrderRequestView},{rejectWithValue}):Promise<{msg:string,data:OrderResponseView,status:string}|any>=>{
      try{
        if(AuthUtil.isSetTokenToRequestHeader()){
      const{order} = payload;
      
        let response= await orderService.placeOrder(order);
       
        return response.data;

        }
      }catch(err:any){
           if(!err.response){
            throw err
           }
           return rejectWithValue(err.response.data);
      }
    })
      /**
 * @usage: get all  orders
 * @url: http://localhost:9000/api/orders/
 * @params: no-params
 * @method: get
 * @access: private
 */
      export const getAllOrdersAction:any = createAsyncThunk('users/getAllOrdersAction',
        async(payload:{},{rejectWithValue}):Promise<{msg:string,data:OrderResponseView[],status:string}|any>=>{
          try{
            if(AuthUtil.isSetTokenToRequestHeader()){
            let response= await orderService.getAllOrders();
            return response.data;
            }
          }catch(err:any){
               if(!err.response){
                throw err
               }
               return rejectWithValue(err.response.data);
          }
        })
         /**
 * @usage: get my  order
 * @url: http://localhost:9000/api/orders/me
 * @params: no-params
 * @method: get
 * @access: private
 */
         export const getmyOrderAction:any = createAsyncThunk('users/getmyOrderAction',
            async(payload:{},{rejectWithValue}):Promise<{msg:string,data:OrderResponseView[],status:string}|any>=>{
              try{
                if(AuthUtil.isSetTokenToRequestHeader()){
                let response= await orderService.getmyOrder();
                return response.data;
                }
              }catch(err:any){
                   if(!err.response){
                    throw err
                   }
                   return rejectWithValue(err.response.data);
              }
            })
/** 
 @usage: updateorderStatus
 * @url: http://localhost:9000/api/orders/:orderId
 * @params:orderstatus
 * @method: put
 * @access: private
 */
 export const updateOrderStatusAction:any = createAsyncThunk('users/updateOrderStatusAction',
    async(payload:{orderstatus:string,orderId:string},{rejectWithValue}):Promise<{msg:string,data:OrderResponseView,status:string}|any>=>{
      try{
        if(AuthUtil.isSetTokenToRequestHeader()){
       const{orderstatus,orderId}=payload
        let response= await orderService.updateOrderStatus(orderstatus,orderId);
        return response.data;
        }
      }catch(err:any){
           if(!err.response){
            throw err
           }
           return rejectWithValue(err.response.data);
      }
    })