import { CartNewResponseView, CartResponseView, Productentity, TheProduct } from "../../Modules/cart/models/CartResponseView";
import { ProductResponseView } from "../../Modules/products/models/ProductResponseView";
import { toastUtil } from "../../Util/ToastUtil";

export class CartReduxService{
    private static PRODUCT_TAX:number =5.0;

    public static convertToProductEntity(product:ProductResponseView):TheProduct{
        return{
           _id:product._id,
           count:product.count,
           price:product.price,
           quantity:product.quantity,
           imageurl:product.imageurl,
           brand:product.brand,
           categoryObj:product.categoryObj._id,
           subCategoryObj:product.subCategoryObj._id,
           title:product.title,
           description:product.description,
           userObj:product.userObj._id,
           sold:product.sold
        }
   }
   public static addToCartUtil(cart:CartNewResponseView,product:TheProduct,count:number):CartNewResponseView{
    //check if the item already exists or not
    const existingProduct:TheProduct|undefined = cart.products.find(item =>  item._id === product._id)
    if(existingProduct){
        toastUtil.displayErrorMessage("Item already exist in the cart");
        return cart;
    }else
    {
         toastUtil.displaySuccessMessage("Item added to the cart")
        return{
            ...cart,
            products: [...cart.products,{...product,count:count}]  

        };

    }
      
         
   }
   public static incrementCartUtil(cart:CartNewResponseView,productId:string):CartNewResponseView{
    return{
        ...cart,
        products:cart.products.map(product=>{
            if(product._id === productId){
                return{
                    ...product,
                    count:product.count + 1

                }
         
            }
        
           return product;
        })
    }
}
public static decrementCartUtil(cart:CartNewResponseView,productId:string):CartNewResponseView{
    return{
        ...cart,
        products:cart.products.map(product=>{
            if(product._id === productId){
                return{
                    ...product,
                    count:product.count -1>0? product.count -1 :1

                }
         
            }
        
           return product;
        })
    }
}
public static deleteProductItemUtil(cart:CartNewResponseView,productId:string):CartNewResponseView{
    toastUtil.displayInfoMessage("product is deleted successfully")
    return{
        ...cart,
        products:cart.products.filter(product => product._id !== productId)
}
}
public static calculateTotal(products:TheProduct[]):number{
       let total:number=0;
    for(let product of products){
        total += Number(product.price)* Number(product.count)
    }
    return total;
}
public static calculateTax(products:TheProduct[]):number{
    const total = this.calculateTotal(products)
    return total *this.PRODUCT_TAX / 100;
}
public static calculategrandTotal(products:TheProduct[]):number{
    const grandTotal = this.calculateTotal(products)+this.calculateTax(products)
    return grandTotal;
}
public static convertProductNewResponseView(products:Productentity[]):TheProduct[]{
    return products.map((item)=>{
        return{
            _id:item.product._id,
            count:item.count,
            price:item.product.price,
            quantity:item.product.quantity,
            imageurl:item.product.imageurl,
            brand:item.product.brand,
            categoryObj:item.product.categoryObj,
            subCategoryObj:item.product.subCategoryObj,
            title:item.product.title,
            description:item.product.description,
            userObj:item.product.userObj,
            sold:item.product.sold
         }
    })
}
}