import React, { useEffect } from 'react'
import { Col, ListGroup, ListGroupItem, Row } from 'react-bootstrap'
import { Container } from 'react-bootstrap'
import { Link, useParams } from 'react-router-dom'
import MainNavbar from '../../../layouts/pages/Navbar/MainNavbar'
import Layoutheading from '../../../layouts/components/Layoutheading/Layoutheading'
import { Button } from 'react-bootstrap'
import { Appdispatch, RootState, useAppDispatch } from '../../../../Redux/store'
import * as productactions from "../../../../Redux/products/product.actions";
import * as productReducer from "../../../../Redux/products/product.reducer";
import { useSelector } from 'react-redux'
import { ProductResponseView } from '../../models/ProductResponseView'
import { addToCartAction } from '../../../../Redux/carts/cart.reducer'
import { CartReduxService } from '../../../../Redux/carts/cartReduxService'

const ViewProduct = () => {
    const dispatch:Appdispatch = useAppDispatch();
    const {categoryName,productId} =useParams();

    //get data from redux
    const productReduxState:productReducer.IntialState = useSelector((state:RootState)=>{
        return state[productReducer.productFeatureKey]
    })
     const {loading,product}=productReduxState;

    useEffect(()=>{
       dispatch(productactions.getProductAction({productId:productId}))
    },[productId])

    const ClickAddToCart=(product:ProductResponseView)=>{
      dispatch({
        type:`${addToCartAction}`,
        payload:{product:CartReduxService.convertToProductEntity(product),count:1}
      });
    }
      
  return (
    <div>
            <MainNavbar/>
        <Container>
            
            <Layoutheading heading={'View Products'}/>
            <Row>
                <Col xs={5}>
                <img src={product.imageurl} alt=''className='img-fluid rounded-3 shadow-lg' ></img>
                <Link to={`/products/${categoryName?.toLowerCase()}`} className='btn btn-warning  mt-2'>
                <i className='bi bi-arrow-left-circle-fill'></i> Back</Link>
                </Col>
                <Col xs={6}>
                <ListGroup>
                    <ListGroupItem>Name:<span className='fw-bold'>{product.title}</span></ListGroupItem>
                    <ListGroupItem>Brand:<span className='fw-bold'>{product.brand}</span></ListGroupItem>
                    <ListGroupItem>Price:<span className='fw-bold'>{product.price}</span></ListGroupItem>
                    <ListGroupItem>Qty:<span className='fw-bold'>{product.quantity}</span></ListGroupItem>
                    <ListGroupItem>Description:<span>{product.description}</span></ListGroupItem>
                    <ListGroupItem>Category:<span className='fw-bold'>{product.categoryObj?.name}</span></ListGroupItem>
                    <ListGroupItem>SubCategory:<span className='fw-bold'>{product.subCategoryObj?.name}</span></ListGroupItem>
                   
                   
                </ListGroup>
                <Button variant={'warning'} className='mt-2' onClick={()=>ClickAddToCart(product)}>Add to Cart</Button>
                </Col>
            </Row>
        </Container>
    </div>
  )
}

export default ViewProduct
