import React from 'react'
import MainNavbar from '../../../layouts/pages/Navbar/MainNavbar'
import Layoutheading from '../../../layouts/components/Layoutheading/Layoutheading'
import { Card, Container, ListGroup, ListGroupItem, Table } from 'react-bootstrap'
import { Col, Row } from 'react-bootstrap'
import { Button } from 'react-bootstrap'
import { Appdispatch, RootState, useAppDispatch } from '../../../../Redux/store'
import * as cartReducer from "../../../../Redux/carts/cart.reducer"
import * as cartActions from "../../../../Redux/carts/cart.actions";
import { useSelector } from 'react-redux'
import Noproducts from '../../../ui/components/No_Products/Noproducts'
import { CartReduxService } from '../../../../Redux/carts/cartReduxService'
import { CartRequestView } from '../../models/CartRequestView'
import { useNavigate } from 'react-router-dom'
import * as useractions from "../../../../Redux/users/user.actions"
import * as userReducer from "../../../../Redux/users/user.reducer";
import { Userview } from '../../../users/models/Userview'
const CartPage = () => {
const dispatch:Appdispatch= useAppDispatch();
const navigate = useNavigate();

//get data from redux
const cartReduxState:cartReducer.IntialState =useSelector((State:RootState)=>{
return State[cartReducer.cartFeatureKey]
})
//get the data from store
  const userState:userReducer.IntialState = useSelector((state:RootState)=>{
    return state[userReducer.userFeatureKey]
  })

  const {user} =userState;
const {cartproduct}=cartReduxState;
const {products}=cartproduct;

 const clickIncreQty=(productId:string|undefined)=>{
  if(productId){
    dispatch({
      type:`${cartReducer.increProductQtyAction}`,
      payload:{productId:productId}
    });
  }
 }
 const clickDecreQty=(productId:string|undefined)=>{
  if(productId){
    dispatch({
      type:`${cartReducer.decreProductQtyAction}`,
      payload:{productId:productId}
    });
  }
 }
 const clickDelete=(productId:string|undefined)=>{
  if(productId){
    dispatch({
      type:`${cartReducer.deleteToCartItemAction}`,
      payload:{productId:productId}
    });
  }
 }
 const preparecartRequestObject=(user:Userview):CartRequestView=>{
      const cart: CartRequestView ={
        products:products.map(product=>{
          return{
           product: product._id,
           price:product.price,
           count:product.count
          }
        }),
        tax:CartReduxService.calculateTax(products),
        total:CartReduxService.calculateTotal(products),
        grandtotal:CartReduxService.calculategrandTotal(products),
        userObj:user?._id
      }
      return cart;
 }
 const clickcheckOut=()=>{
   if(user && Object.keys(user).length>0){
    dispatch(cartActions.createCartAction({cart:preparecartRequestObject(user)})).then((response:any)=>{
      if(response && !response.error){
          navigate("/cart/checkout")
      }
})
   }
 }
  return (
    <div>
      <MainNavbar/>
      <Layoutheading heading={"Your Cart"} icon={"bi bi-cart-fill"}/>
      {
        cartproduct.products.length>0 &&
        <Container>
        <Row>
          <Col xs={9}>
          <Card className='shadow-lg'>
            <Card.Header className='bg-success text-white'>
              <p className='h4'>Cart Items</p>
            </Card.Header>
            <Card.Body className='bg-light-grey'>
              <Table striped hover className='text-center'>
                    <thead className='bg-warning'>
                      <tr >
                        <th>SNo</th>
                        <th>Image</th>
                        <th>Title</th>
                        <th>Price</th>
                        <th>Quantity</th>
                        <th>Total</th>
                        <th>Actions</th>
                      </tr>
                    </thead>
                    <tbody>
                      {
                        products && products.map((product,index)=>{
                          return(
                            <tr key={product._id}>
                              <td>{index+1}</td>
                            <td><img src={product.imageurl} alt="" width={50} height={50}></img></td>
                            <td>{product.title}</td>
                            <td>&#8377;{product.price}</td>
                            <td>
                              <i className='bi bi-dash-circle-fill' onClick={()=>clickDecreQty(product._id)}></i>{product.count}<i className='bi bi-plus-circle-fill' onClick={()=>clickIncreQty(product._id)}></i></td>
                            <td>&#8377;{product.price*product.count}</td>
                            <td><Button variant='danger' onClick={()=>clickDelete(product._id)}>
                              <i className='bi bi-trash-fill'></i></Button></td>
                            </tr>
                          )
                        })
                      }

                    </tbody>
              </Table>
            </Card.Body>
          </Card>
          </Col>
          <Col>
          <Card className='shadow-lg'>
            <Card.Header className='bg-success text-white'>
              <p className='h4'>Cart Items</p>
            </Card.Header>
            <Card.Body>
             <ListGroup>
              <ListGroupItem >Total:<span className='fw-bold'>&#8377;{CartReduxService.calculateTotal(products).toFixed(2)}</span></ListGroupItem>
              <ListGroupItem>Tax:<span className='fw-bold'>&#8377;{CartReduxService.calculateTax(products).toFixed(2)}</span></ListGroupItem>
              <ListGroupItem>GrandTotal:<span className='fw-bold'>&#8377;{CartReduxService.calculategrandTotal(products).toFixed(2)}</span></ListGroupItem>
              <div className='d-grid gap-2 mt-3'>
              <Button variant={'warning'} onClick={()=>clickcheckOut()}>CheckOut</Button>
              </div>
             </ListGroup>
            </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
      }
      {
        products.length === 0 &&
        <Noproducts/>
      }
     
    </div>
  )
}

export default CartPage
