import axios from "axios";
import { CartRequestView } from "../models/CartRequestView";
import { CartResponseView } from "../models/CartResponseView";

export class CartService{
    private static serverUrl :string =  process.env.REACT_APP_EXPRESS_SERVER_URL? process.env.REACT_APP_EXPRESS_SERVER_URL: "";
    /**
 * @usage: create a cart
 * @url: http://localhost:9000/api/carts/
 * @params: products[{product,count,price}],total,tax,grandtotal
 * @method: post
 * @access: private
 */
    public static createCart(cart:CartRequestView):Promise<{data:{msg:string,data:CartResponseView,status:string}}>{
        const dataUrl=`${this.serverUrl}/api/carts/`;
        return axios.post(dataUrl,cart);
    } ;
    /**
* @usage: get cart info
* @url: http://localhost:9000/cart/me
* @params: no-params
* @method: get
* @access: private
*/
public static getCartInfo():Promise<{data:{msg:string,data:CartResponseView,status:string}}>{
    const dataUrl=`${this.serverUrl}/api/carts/me`;
    return axios.get(dataUrl);
} ;
}