import React from 'react'
import  * as orderReducer from "../../../../Redux/orders/orders.reducer"
import { useSelector } from 'react-redux'
import {  RootState } from '../../../../Redux/store'
import SpinnerUI from '../../../ui/components/SpinnerUI/SpinnerUI'
import MainNavbar from '../../../layouts/pages/Navbar/MainNavbar'
import Layoutheading from '../../../layouts/components/Layoutheading/Layoutheading'
import { Container } from 'react-bootstrap'
import { ListGroupItem, Row } from 'react-bootstrap'
import { Col } from 'react-bootstrap'
import { ListGroup } from 'react-bootstrap'
import orderImg from "../../../../Assets/orderimg3.png";
import { Link } from 'react-router-dom'
import { Button } from 'react-bootstrap'


const OrderDetails = () => {
  

  const orderReduxState:orderReducer.IntialState = useSelector((State:RootState)=>{
    return State[orderReducer.orderFeatureKey]
  })
  const{orderproduct,loading} = orderReduxState;
  
  return (
    <div>
      { loading && <SpinnerUI/>}
      <MainNavbar/>
      <Layoutheading heading={'OrderDetails'}/>
      <Container>
      {
            orderproduct && Object.keys(orderproduct).length>0 &&
        <Row>
          
          <Col sm={2}>
          <img src={orderImg} alt="/" className='img-fluid rounded-circle'></img>
          </Col>
          <Col sm={6}>
          
            <ListGroup>
            <ListGroupItem className='fw-bold text-success'>
              Order is placed successfully
            </ListGroupItem>
            <ListGroupItem>
              OrderNumber: <span className='fw-bold'>{orderproduct._id}</span>
            </ListGroupItem>
            <ListGroupItem> 
              Total Amount: <span className='fw-bold'>&#8377;{Number(orderproduct.grandtotal).toFixed(2)}</span>
            </ListGroupItem>

            <ListGroupItem>
              OrderStatus: <span className='fw-bold'>{orderproduct.orderstatus}</span>
              </ListGroupItem>
              <ListGroupItem>
              Paymentmode: <span className='fw-bold'>{orderproduct.paymentType}</span>
              </ListGroupItem>
          </ListGroup>
            <Link to={"/products/fashion"} >
            <Button variant={'warning'} className='mt-2'>Continue Shopping</Button>
            </Link>
          
          </Col>
        </Row>
}
      </Container>

    </div>
  )
}

export default OrderDetails
